import React from 'react'
import Typography from '@material-ui/core/Typography/Typography'
import CreateAnchorService from '../../services/create-anchor-service'
import getContentPerTypeService from '../../services/content-converter-service'

export default class DisplayHeader extends React.Component {
  constructor(props) {
    super(props)

    /* Pour ajouter de nouveaux éléments dans les h2, il suffit de les ajouter à la suite de ce tableau
     * puis de créer le composant correspondant dans le render */
    this.typeList = ['tocLabel']
  }
  render() {
    const { content, type } = this.props

    const contentPerType = getContentPerTypeService(this.typeList, content)
    return (
      <Typography color="secondary" variant={type} id={CreateAnchorService(contentPerType[0].value)}>
        {contentPerType.map((contentPT, index) => {
          return (() => {
            switch (contentPT.type) {
              case this.typeList[0]:
                return (
                  <span key={index} style={{ display: 'none' }}>
                    {`[tocLabel]${contentPT.value}[/tocLabel]`}
                  </span>
                )
              default:
                return <span key={index}>{contentPT.value}</span>
            }
          })()
        })}
      </Typography>
    )
  }
}
