import tocbot from 'tocbot'
import React from 'react'
import Drawer from '@material-ui/core/Drawer'
import Icon from '@material-ui/core/Icon'
import Fab from '@material-ui/core/Fab'
import { withStyles } from '@material-ui/core'

const tocLabelTag = '[tocLabel]'

const styles = theme => ({
  drawer: {
    '&>div:nth-child(2)': {
      maxWidth: 300,
      width: '80%',
      backgroundColor: theme.palette.tertiary.main,
      '&>div': {
        maxHeight: 'calc(100vh - 50px)',
      },
    },
  },
})

class DisplayArticleToc extends React.Component {
  state = {
    windowAbove960: window.innerWidth > 960,
    wrapperScrollStyle: { top: 'auto' },
    commentsOffsetTop: 10000,
    drawerOpen: false,
  }

  componentDidMount = () => {
    const { windowAbove960 } = this.state

    if (windowAbove960) {
      this.initTocbot()
      document.addEventListener('scroll', this.handleDocumentScroll)
    }
    window.addEventListener('resize', this.handleWindowResize)
  }

  componentWillUnmount = () => {
    const { windowAbove960 } = this.state
    tocbot.destroy()
    if (windowAbove960) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
    }
    window.removeEventListener('resize', this.handleWindowResize)
  }

  componentDidUpdate = (prevProps, prevState) => {
    if (prevProps.article.id !== this.props.article.id) {
      setTimeout(() => {
        /* Refresh tocbot after rendering */
        tocbot.refresh()
      }, 0)
    } else if (
      (!prevState.drawerOpen && this.state.drawerOpen) ||
      (!prevState.windowAbove960 && this.state.windowAbove960)
    ) {
      setTimeout(() => {
        this.initTocbot()
      }, 0)
    } else if (
      (prevState.drawerOpen && !this.state.drawerOpen) ||
      (prevState.windowAbove960 && !this.state.windowAbove960)
    ) {
      tocbot.destroy()
    }
  }

  initTocbot = () => {
    tocbot.init({
      tocSelector: '.toc-here',
      contentSelector: '.article-wrapper',
      headingSelector: 'h2, h3, h4',
      ignoreSelector: '.js-toc-ignore',
      headingLabelCallback: heading => {
        const index = heading.indexOf(tocLabelTag)

        if (index !== -1) {
          const tocData = heading.slice(index + tocLabelTag.length)
          heading = tocData.slice(0, tocData.length - (tocLabelTag.length + 1))
        }

        if (heading.length > 60) {
          const words = heading.substring(0, 60).split(' ')
          words.splice(-1, 1) // remove last word (often truncated)
          heading = words.join(' ') + '...'
        }

        return heading
      },
      onClick: e => {
        e.preventDefault()
        window.history.replaceState({}, e.target.innerHTML, window.location.href.split('#')[0] + e.target.hash)
      },
    })
  }

  handleWindowResize = () => {
    const { windowAbove960 } = this.state
    if (window.innerWidth > 960 && !windowAbove960) {
      document.addEventListener('scroll', this.handleDocumentScroll)
      this.setState({ windowAbove960: true, drawerOpen: false })
    } else if (window.innerWidth <= 960 && windowAbove960) {
      document.removeEventListener('scroll', this.handleDocumentScroll)
      this.setState({ windowAbove960: false, wrapperScrollStyle: { top: 'auto', position: 'initial', width: '100%' } })
    }
  }

  handleDocumentScroll = () => {
    const { wrapperScrollStyle, commentsOffsetTop } = this.state
    if (
      window.scrollY >= 84 &&
      (wrapperScrollStyle.top === 'auto' || (window.innerWidth < 1400 && wrapperScrollStyle.width.includes('15%'))) &&
      window.scrollY < commentsOffsetTop
    ) {
      this.setState({
        wrapperScrollStyle: { top: 0, position: 'fixed', width: 'calc(20% + 20px)' },
        commentsOffsetTop: document.querySelector('#comments-list').offsetTop - 700,
      })
    } else if (
      window.scrollY >= 84 &&
      (wrapperScrollStyle.top === 'auto' || (window.innerWidth > 1400 && wrapperScrollStyle.width.includes('20%'))) &&
      window.scrollY < commentsOffsetTop
    ) {
      this.setState({
        wrapperScrollStyle: { top: 0, position: 'fixed', width: 'calc(15% + 20px)' },
        commentsOffsetTop: document.querySelector('#comments-list').offsetTop - 700,
      })
    } else if (window.scrollY < 84 && wrapperScrollStyle.top === 0) {
      this.setState({ wrapperScrollStyle: { top: 'auto', position: 'initial', width: '100%' } })
    } else if (window.scrollY > commentsOffsetTop && wrapperScrollStyle.top === 0) {
      this.setState({ wrapperScrollStyle: { top: 'auto', position: 'initial', width: '100%' } })
    }
  }

  render = () => {
    const { wrapperScrollStyle, windowAbove960, drawerOpen } = this.state
    const { classes } = this.props

    return (
      <div className="table-of-content" style={wrapperScrollStyle}>
        {windowAbove960 ? (
          <>
            <h2>Sommaire :</h2>
            <div className="toc-wrapper">
              <div className="toc-here" />
            </div>
          </>
        ) : (
          <>
            <Fab
              size="medium"
              color="primary"
              style={{ position: 'fixed', right: 20, bottom: 20, zIndex: 9999 }}
              onClick={() => this.setState(({ drawerOpen }) => ({ drawerOpen: !drawerOpen }))}
            >
              <Icon>list</Icon>
            </Fab>
            <Drawer
              anchor="right"
              open={drawerOpen}
              onClose={() => this.setState({ drawerOpen: false })}
              className={classes.drawer}
            >
              <div className="table-of-content" style={(wrapperScrollStyle, { padding: '0 10px' })}>
                <h2 style={{ textAlign: 'center' }}>Sommaire :</h2>
                <div className="toc-wrapper">
                  <div className="toc-here" />
                </div>
              </div>
            </Drawer>
          </>
        )}
      </div>
    )
  }
}

export default withStyles(styles)(DisplayArticleToc)
